import { connect } from "react-redux";
import { Link }    from "react-router-dom";

//Import Own Components
import AuthForm from "Components/AuthForm";
import "./EmailSendPage.scss";

const EmailSendPage = ({
	email,
	delegations : {
		handleHideEmail,
	},
}) => {
	return (
		<div id="EmailSendPage">
			<AuthForm
				Footer={Footer}
				showBtn={false}
				title="¡Correo Enviado!"
				subTitle="Hemos enviado las intrucciones para restablecer tu contraseña al siguiente correo:"
			>
				<p className="email">{handleHideEmail(email)}</p>
			</AuthForm>
		</div>
	);
};
const Footer = () => (
	<>
		<p>Voler a </p>
		<Link to="/auth/login">Inicio Sesión.</Link>
	</>
);

const mapDispatchToState = ({ _authSlice }) => ({
	email : _authSlice?.user?.email ?? "",
});

export default connect(mapDispatchToState)(EmailSendPage);
