
//Import Own Components
import EmailSendPage from "./EmailSendPage";

const EmailSendPageContainer = () => {

	const handleHideEmail = (email = "") => {
		const emailSplited = email.split("@");

		if (emailSplited.length < 2) return email;

		const censorWord = emailSplited[0];

		if (censorWord.length <= 1) {
			return  "*".repeat(1) +  "@" + emailSplited[1];
		}

		if (censorWord.length <= 2) {
			return censorWord[0] + "*".repeat(censorWord.length - 1) +  "@" + emailSplited[1];
		}

		return censorWord[0] + "*".repeat(censorWord.length - 2) + censorWord.slice(-1) + "@" + emailSplited[1];
	};
	return (
		<EmailSendPage
			delegations={{
				handleHideEmail,
			}}
		/>
	);
};

export default EmailSendPageContainer;
