import {
	Form,
	Card,
	CardBody,
} from "reactstrap";

// Own Components
import HandleImage from "Components/Global/HandleImage";
import logo        from "Resources/images/Logotipo.png";
import "./AuthForm.scss";
import SubmitBtn   from "Components/Global/SubmitBtn";

const AuthForm = ({
	title,
	Footer,
	showBtn,
	subTitle,
	children,
	labelBtn,
	delegations : {
		loading,
		handleSubmit,
	},
}) => {
	return (
		<div id="AuthForm">
			<HandleImage
				image={logo}
				className="mb-1"
			/>
			<Card className="card-style">
				<CardBody className="card-body-style">
					<h1 className="title">{title}</h1>
					<p className="sub-tilte mb-2">{subTitle}</p>
					<Form className="form" onSubmit={handleSubmit}>
						{children}
						<SubmitBtn
							label={labelBtn}
							show={showBtn}
							loading={loading}
							handleOnclick={handleSubmit}
						/>
					</Form>
				</CardBody>
				{
					Footer ?
						<div className="footer-wrapper">
							<Footer />
						</div>
						:
						<div className="mb-1" />
				}
			</Card>
		</div>
	);
};

export default AuthForm;
